/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";

  font-display: swap;
  src: local("PT Mono"),
    url("../fonts/pt-mono-regular.woff2") format("woff2"),
    url("../fonts/pt-mono-regular.woff") format("woff");
}

/* roboto-condensed-regular - latin_cyrillic */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";

  font-display: swap;
  src: local("Roboto Condensed"),
    url("../fonts/roboto-condensed-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-regular.woff") format("woff");
}
/* roboto-condensed-700 - latin_cyrillic */
@font-face {
  font-style: normal;
